"use client"

import * as React from "react"
import { Bell, Search, Moon, Sun, ChevronDown, User, Settings, LogOut, Mail, Phone, Calendar, Shield } from 'lucide-react'
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu"
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar"
import {
  Sheet,
  SheetContent,
  SheetDescription,
  SheetHeader,
  SheetTitle,
} from "@/components/ui/sheet"
import Link from "next/link"
import { useRouter } from "next/navigation"
import axiosClient from "@/lib/axiosClient"
import { showToast } from "@/lib/showToast"

interface TopNavProps {
  onMenuClick: () => void
}

export function TopNav({ onMenuClick }: TopNavProps) {
  const [theme, setTheme] = React.useState<"light" | "dark">("light")
  const [userdetail, setUserdetail] = React.useState<any>({})
  const [isProfileOpen, setIsProfileOpen] = React.useState(false) // For sheet

  const router = useRouter()

  const toggleTheme = () => {
    const newTheme = theme === "light" ? "dark" : "light"
    setTheme(newTheme)
    document.documentElement.classList.toggle("dark", newTheme === "dark")
  }

  function getInitials(name: string) {
    if (!name) return "U"
    const parts = name.trim().split(" ")
    return parts.length >= 2
      ? parts[0][0] + parts[1][0]
      : parts[0][0]
  }

  const handleLogout = async () => {
    try {
      await axiosClient.post("/auth/logout")
      localStorage.removeItem("token")
      localStorage.removeItem("user")
      router.push("/signin")
    } catch (error) {
      console.error("Logout failed:", error)
      showToast("Logout failed", "error")
    }
  }

  const getUser = async () => {
    try {
      const res = await axiosClient.get("/auth/me")

      if (!res.data?.data) {
        showToast("Unable to load your profile.", "error")
        return
      }

      setUserdetail(res.data.data)
    } catch (error: any) {
      console.error("Get user error:", error)

      if (error.response?.status === 401) {
        showToast("Session expired. Please login again.", "error")
        localStorage.clear()
        router.replace("/signin")
      } else {
        showToast("Failed to load profile.", "error")
      }
    }
  }

  React.useEffect(() => {
    document.documentElement.classList.remove("dark")
    getUser()
  }, [])

  return (
    <>
      <header className="sticky top-0 z-30 flex h-16 items-center gap-4 border-b border-gray-200 bg-white dark:border-gray-800 dark:bg-gray-950 px-6 justify-around">
        <div className="flex-1 max-w-xl">
          <div className="relative">
            <Search className="absolute left-3 top-1/2 -translate-y-1/2 w-5 h-5 text-gray-400" />
            <Input
              type="search"
              placeholder="Search or type command..."
              className="pl-10 pr-20 bg-gray-50 border-gray-200 focus:bg-white h-10"
            />
            <kbd className="absolute right-3 top-1/2 -translate-y-1/2 px-2 py-1 text-xs font-medium text-gray-500 bg-white border border-gray-200 rounded">
              ⌘K
            </kbd>
          </div>
        </div>

        {/* Right section */}
        <div className="flex items-center gap-3">
          {/* Theme Toggle */}
          <Button
            variant="ghost"
            size="icon"
            onClick={toggleTheme}
            className="h-9 w-9 rounded-full"
          >
            {theme === "light" ? <Moon className="h-5 w-5" /> : <Sun className="h-5 w-5" />}
          </Button>

          {/* Notifications */}
          <DropdownMenu>
            <DropdownMenuTrigger asChild>
              <Button variant="ghost" size="icon" className="relative h-9 w-9 rounded-full">
                <Bell className="h-5 w-5" />
                <span className="absolute top-1 right-1 w-2 h-2 bg-red-500 rounded-full" />
              </Button>
            </DropdownMenuTrigger>
            <DropdownMenuContent align="end" className="w-80">
              <DropdownMenuLabel>Notifications</DropdownMenuLabel>
              <DropdownMenuSeparator />
              <div className="p-4 text-center text-sm text-gray-500">
                No notifications yet
              </div>
            </DropdownMenuContent>
          </DropdownMenu>

          {/* User Menu */}
          <DropdownMenu>
            <DropdownMenuTrigger asChild>
              <Button variant="ghost" className="h-9 gap-2 px-3 rounded-full hover:bg-gray-100">
                <Avatar className="h-9 w-9 border">
                  <AvatarImage src={userdetail?.avatar} alt={userdetail?.name} />
                  <AvatarFallback className="bg-blue-600 text-white">
                    {getInitials(userdetail?.name || "")}
                  </AvatarFallback>
                </Avatar>
                <span className="text-sm font-medium hidden md:inline">
                  {userdetail?.name || "Loading..."}
                </span>
                <ChevronDown className="h-4 w-4 text-gray-500" />
              </Button>
            </DropdownMenuTrigger>

            <DropdownMenuContent align="end" className="w-56">
              <DropdownMenuLabel>My Account</DropdownMenuLabel>
              <DropdownMenuSeparator />

              {/* This opens the sliding profile panel */}
              <DropdownMenuItem onClick={() => setIsProfileOpen(true)}>
                <User className="mr-2 h-4 w-4" />
                <span>Profile</span>
              </DropdownMenuItem>

              <DropdownMenuItem asChild>
                <Link href="/settings" className="flex items-center">
                  <Settings className="mr-2 h-4 w-4" />
                  Settings
                </Link>
              </DropdownMenuItem>

              <DropdownMenuSeparator />
              <DropdownMenuItem onClick={handleLogout} className="text-red-600 focus:text-red-600">
                <LogOut className="mr-2 h-4 w-4" />
                Logout
              </DropdownMenuItem>
            </DropdownMenuContent>
          </DropdownMenu>
        </div>
      </header>

      {/* Sliding Profile Drawer */}
      <Sheet open={isProfileOpen} onOpenChange={setIsProfileOpen}>
        <SheetContent side="right" className="w-96">
          <SheetHeader className="px-6 pt-6">
            <SheetTitle>Profile Details</SheetTitle>
            <SheetDescription>
              Your account information
            </SheetDescription>
          </SheetHeader>

          <div className="mt-6 px-6 py-8">
            <div className="flex flex-col items-center text-center">
              <Avatar className="h-24 w-24 border-4 border-white shadow-lg">
                <AvatarImage src={userdetail?.avatar} />
                <AvatarFallback className="text-2xl bg-blue-600 text-white">
                  {getInitials(userdetail?.name || "")}
                </AvatarFallback>
              </Avatar>
              <h3 className="mt-4 text-xl font-semibold">{userdetail?.name || "—"}</h3>
              <p className="text-sm text-gray-500">{userdetail?.role || "User"}</p>
            </div>

            <div className="mt-8 space-y-5">
              <div className="flex items-center gap-4 text-sm">
                <Mail className="h-5 w-5 text-gray-400" />
                <div>
                  <p className="text-gray-500">Email</p>
                  <p className="font-medium">{userdetail?.email || "—"}</p>
                </div>
              </div>

              {userdetail?.phone && (
                <div className="flex items-center gap-4 text-sm">
                  <Phone className="h-5 w-5 text-gray-400" />
                  <div>
                    <p className="text-gray-500">Phone</p>
                    <p className="font-medium">{userdetail.phone}</p>
                  </div>
                </div>
              )}

              {userdetail?.department && (
                <div className="flex items-center gap-4 text-sm">
                  <Shield className="h-5 w-5 text-gray-400" />
                  <div>
                    <p className="text-gray-500">Department</p>
                    <p className="font-medium">{userdetail.department}</p>
                  </div>
                </div>
              )}

              <div className="flex items-center gap-4 text-sm">
                <Calendar className="h-5 w-5 text-gray-400" />
                <div>
                  <p className="text-gray-500">Joined</p>
                  <p className="font-medium">
                    {userdetail?.created_at ? new Date(userdetail.created_at).toLocaleDateString() : "—"}
                  </p>
                </div>
              </div>
            </div>

            <div className="mt-10 flex gap-3">
              <Button variant="outline" className="flex-1" asChild>
                <Link href="/settings">Edit Profile</Link>
              </Button>
              <Button variant="default" className="flex-1" onClick={() => setIsProfileOpen(false)}>
                Close
              </Button>
            </div>
          </div>
        </SheetContent>
      </Sheet>
    </>
  )
}